body {
    padding-top: 50px;
    padding-bottom: 20px;
}

.navbar-inverse .navbar-brand {
    color: #4935b3;
    font-size: 30px;
}

.navbar-header a:hover {
    color: #4935b3 !important;
}

.navbar-nav {
    padding-left: 190px;
}

#home {
    color: #4935b3;
    font-size: 16px;
}

.navbar-inverse .navbar-nav>li>a {
    color: #ffffff;
}

#element-with-background-image {
    position: relative;
    background-image: url(../img/castle.jpg) !important;
    background-position: center;
    background-size: cover;
    background-color: transparent;
    min-height: 400px;
}

#color-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #4a4848;
    opacity: 0.6;
}

#color-overlay h2 {
    color: #ffffff;
    font-family: Aharoni !important;
    font-size: 40PX;
    text-align: center;
    padding-top: 120px;
}

#color-overlay p {
    text-align: center;
}

#btn {
    background-color: #4a4848;
}

section h3 {
    font-size: 30px;
    text-align: center;
    font-family: aharoni;
}

section a:hover {
    text-decoration: none;
}

section p {
    text-align: center;
    font-weight: 20px;
    padding-bottom: 50px;
}